// forms
form,
fieldset {
  margin: 0;
  border-style: none;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
//input[type='url'],
// input[type='date'],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  outline: none;
  box-sizing: border-box;
  border: 1px solid #edeff4;
  color: $black;
  // &:hover {

  // }

  &:focus {
    border-color: $form-element-focus-border-color;
  }

  @include placeholder {
    color: $placeholder-color;
  }
}

input[attribute="value"] {
}

select {
  -webkit-border-radius: 0;
}

textarea {
  vertical-align: top;
  resize: none;
}

button,
input[type="button"],
input[type="reset"],
input[type="file"],
input[type="submit"] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer;
  font-family: $base-font-sans-serif;

  &:hover {
  }
}
